<template>
  <div :class="getClass()" v-on="{mouseenter, mouseleave}" @click.prevent="strike"></div>
</template>

<script>
  export default {

    name: 'Cell',

    props: ['order'],

    data () {
      return {
        mark: '',
        empty: true,
        hover: false,
        frozen: false
      }
    },

    watch: {
      mark: function () {
        if (this.length) {
          this.empty = false
        }
      }
    },

    computed: {
      activePlayerSign () {
        return this.$root.$children[0].activePlayerSign
      }
    },

    methods: {

      mouseenter () {
        this.empty && !this.frozen ? this.hover = true : ''
      },

      mouseleave () {
        this.empty && !this.frozen ? this.hover = false : ''
      },

      getClass: function () {
        let className = 'cell'

        if (this.hover) {
          className += ` cell--hover-${this.activePlayerSign}`
        }

        if (this.mark.length) {
          className += ` cell--${this.mark}`
        }

        if (!this.empty || this.frozen) {
          className += ' cell--disabled'
        }

        return className
      },

      strike: function () {
        if (this.empty && !this.frozen) {
          this.empty = false
          this.hover = false
          this.mark = this.$root.$children[0].activePlayerSign
          this.$bus.emit('strike', this.order)
        }
      },

    },

    created: function () {

      this.$bus.$on('clearGrid', () => {
        this.mark = ''
        this.empty = true
        this.frozen = false
        // Ячейка очищена
      })

      this.$bus.$on('freezeGrid', () => {
        this.frozen = true
        // Ячейка заморожена
      })

      this.$bus.$on('unfreezeGrid', () => {
        this.frozen = false
        // Ячейка разморожена
      })

    }

  }
</script>

<style lang="less" scoped>
  @keyframes strike {
    from {
      transform: translate(-50%, -50%) scale(.8);
    }
    50% {
      transform: translate(-50%, -50%) scale(1.2);
    }
    to {
      transform: translate(-50%, -50%) scale(1);
    }
  }

  .cell {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    position: relative;
    z-index: 1;
    height: 120px;
    background: #34485d;
    cursor: pointer;

    &::before, &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transition: opacity .15s ease, visibility .15s ease, transform .15s ease;
      transform: translate(-50%, -50%) scale(1);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      opacity: 0;
      visibility: hidden;
    }

    &::before {
      width: 54px;
      height: 50px;
      background-image: url(data:image/svg+xml;base64,);
    }

    &::after {
      width: 46px;
      height: 46px;
      background-image: url(data:image/svg+xml;base64,);
    }

    &--disabled {
      cursor: default;
    }

    &--x {
      &::before {
        opacity: 1;
        visibility: visible;
        animation: strike .2s ease;
      }
    }

    &--o {
      &::after {
        opacity: 1;
        visibility: visible;
        animation: strike .2s ease;
      }
    }

    &--hover-x {
      &::before {
        opacity: .4;
        visibility: visible;
        transform: translate(-50%, -50%) scale(.7);
      }
    }

    &--hover-o {
      &::after {
        opacity: .4;
        visibility: visible;
        transform: translate(-50%, -50%) scale(.7);
      }
    }

  }

  @media only screen and (max-width: 500px), only screen and (max-device-width: 500px) {

    .cell {
      height: 100px;

      &::before {
        width: 42px;
        height: 38px;
      }

      &::after {
        width: 38px;
        height: 38px;
      }

    }

  }
</style>


